import {
    Component,
    Element,
    h,
    Event,
    EventEmitter,
    Prop,
} from '@stencil/core';
import { BreadcrumbItem } from './breadcrumbs.types';
import {
    makeEnterClickable,
    removeEnterClickable,
} from 'src/util/make-enter-clickable';
import { createRandomString } from '../../util/random-string';

/**
 * A Breadcrumb consists of a list of distinct "places" that user has gone through,
 * before ending up where they are right now, in a website or an application.
 *
 * These "places" can be for example _pages_ of a website, which are hierarchically
 * before the current page that the user is looking at.
 * They could also be _steps_ which the user has gone through, which perhaps have no
 * hierarchal relation with each other, but has eventually led the user "here".
 *
 * :::note
 * - Where the user currently is, is always the last step of the breadcrumb.
 * - A breadcrumbs never shows where users can go after this place.
 * It only illustrates where user has been before ending up here.
 * If the path that a user can take is not changing and if next steps are clear,
 * you can use the [Progress flow component](#/component/limel-progress-flow) instead.
 * :::
 *
 * Breadcrumbs are often placed horizontally before the main content of the current screen.
 * @exampleComponent limel-example-breadcrumbs-links
 * @exampleComponent limel-example-breadcrumbs-buttons
 * @exampleComponent limel-example-breadcrumbs-icon-only
 * @exampleComponent limel-example-breadcrumbs-divider
 */

@Component({
    tag: 'limel-breadcrumbs',
    shadow: true,
    styleUrl: 'breadcrumbs.scss',
})
export class Breadcrumbs {
    /**
     * List of items in the breadcrumbs,
     * each representing a step or a page.
     */
    @Prop()
    public items: BreadcrumbItem[];

    /**
     * The visual divider that separates items.
     * It must be a single character such as `-` or `,`.
     */
    @Prop()
    public divider: string = 'â€º';

    /**
     * Fired when clicking on buttons (not links!)
     * inside the breadcrumbs.
     */
    @Event()
    public select: EventEmitter<BreadcrumbItem>;

    @Element()
    private host: HTMLLimelBreadcrumbsElement;

    private button: HTMLButtonElement;
    private anchor: HTMLAnchorElement;

    /**
     * Used to attach the right tooltip to the right item
     */
    private tooltipId: string;

    constructor() {
        this.tooltipId = createRandomString();
    }

    public render() {
        return (
            <ol
                role="navigation"
                aria-label="Breadcrumb"
                style={{ '--limel-breadcrumbs-divider': `'${this.divider}'` }}
            >
                {this.renderSteps(this.items)}
                {this.renderLastStep(this.items)}
            </ol>
        );
    }

    public componentDidRender() {
        this.button = this.host.shadowRoot.querySelector('button');
        this.anchor = this.host.shadowRoot.querySelector('a');
        if (this.button) {
            makeEnterClickable(this.button);
        }

        if (this.anchor) {
            makeEnterClickable(this.anchor);
        }
    }

    public disconnectedCallback() {
        if (this.button) {
            removeEnterClickable(this.button);
        }

        if (this.anchor) {
            removeEnterClickable(this.anchor);
        }
    }

    private renderSteps = (items: BreadcrumbItem[]) => {
        const allStepsWithoutLast = items.slice(0, -1);

        if (this.areItemsLinks(items)) {
            return allStepsWithoutLast.map(this.renderAsLinks);
        }

        return allStepsWithoutLast.map(this.renderAsButtons);
    };

    private renderAsButtons = (item: BreadcrumbItem) => {
        return [
            <button
                role="listitem"
                id={this.tooltipId}
                class="step"
                onClick={this.handleClick(item)}
            >
                {this.renderIcon(item)}
                {this.renderLabel(item)}
            </button>,
            this.renderTooltip(item),
        ];
    };

    private renderAsLinks = (item: BreadcrumbItem) => {
        return [
            <a
                role="listitem"
                id={this.tooltipId}
                class="step"
                href={item.link.href}
                title={item.link.title}
            >
                {this.renderIcon(item)}
                {this.renderLabel(item)}
            </a>,
            this.renderTooltip(item),
        ];
    };

    private renderLastStep = (items: BreadcrumbItem[]) => {
        const lastItem = items.slice(-1);

        return (
            <li
                class="last step"
                aria-current={this.areItemsLinks(items) ? 'page' : 'step'}
            >
                {this.renderIcon(lastItem[0])}
                <span class="text">{lastItem[0].text}</span>
            </li>
        );
    };

    private renderIcon = (item: BreadcrumbItem) => {
        if (!item.icon) {
            return;
        }

        return <limel-icon name={item.icon} />;
    };

    private renderLabel = (item: BreadcrumbItem) => {
        if (item.iconOnly) {
            return;
        }

        return <span class="text">{item.text}</span>;
    };

    private renderTooltip = (item: BreadcrumbItem) => {
        if (!item.iconOnly || !item.text) {
            return;
        }

        return <limel-tooltip elementId={this.tooltipId} label={item.text} />;
    };

    private areItemsLinks = (items: BreadcrumbItem[]) => {
        return items.some((item) => 'link' in item);
    };

    private handleClick = (item: BreadcrumbItem) => (event: MouseEvent) => {
        event.stopPropagation();
        this.select.emit(item);
    };
}
